@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700;800&display=swap");
/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Nunito", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  margin: 0;
  color: #5A5C69;
}

p {
  color: #5A5C69;
  font-size: 2rem;
  margin: 0;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Nunito", sans-serif;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

ul {
  margin: 0;
  padding: 0;
  text-decoration: none;
  overflow-anchor: none;
}
ul li {
  margin: 0;
  padding: 0;
  list-style: none;
}
ul li span {
  font-weight: 900;
  color: #FFFFFF;
}

.flex {
  display: flex;
  position: relative;
}
.flex .navbar-nav {
  display: flex;
  flex-direction: column;
  background-color: #4e73df;
  background-image: linear-gradient(180deg, #4e73df 10%, #224abe 100%);
  background-size: cover;
  width: 12rem;
  transition: 0.3s;
}
.flex .navbar-nav .lateral {
  transition: 0.5s ease-in-out;
}
.flex .navbar-nav.ocultar {
  width: 0;
  transform: translate(-2rem, 0);
  overflow: hidden;
}
@media (min-width: 768px) {
  .flex .navbar-nav {
    width: auto;
  }
  .flex .navbar-nav.small {
    width: 12rem;
  }
}
.flex .navbar-nav p {
  color: #FFFFFF;
}
.flex .navbar-nav.small {
  width: 12rem;
}
.flex .navbar-nav.small .sidebar-brand-text {
  display: none;
}
.flex .navbar-nav.small .nav-caja {
  overflow: hidden;
  opacity: 0;
  transform: scale(0.7);
  visibility: hidden;
  border-radius: 0.5rem;
  background-color: #FFFFFF;
  position: absolute;
  top: 0;
  left: 10.75rem;
  z-index: 1;
  transition: 0.3s;
}
.flex .navbar-nav.small .nav-caja.mostrar {
  box-shadow: 0 0.15rem 1.75rem 0 rgba(58, 59, 69, 0.15);
  opacity: 1;
  transform: scale(1);
  visibility: visible;
}
.flex .navbar-nav.small .nav-link {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: auto;
  transition: 0.3s;
}
.flex .navbar-nav.small .nav-link .bx-arrow {
  display: none;
}
.flex .navbar-nav.small .nav-link .bx-icon {
  font-size: 2rem;
  color: rgba(255, 255, 255, 0.5);
}
.flex .navbar-nav.small .nav-link .span {
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.8);
  font-weight: 600;
}
.flex .navbar-nav.small .nav-link:hover .bx-icon, .flex .navbar-nav.small .nav-link:hover .span, .flex .navbar-nav.small .nav-link:visited .bx-icon, .flex .navbar-nav.small .nav-link:visited .span, .flex .navbar-nav.small .nav-linkfocus .bx-icon, .flex .navbar-nav.small .nav-linkfocus .span, .flex .navbar-nav.small .nav-link:active .bx-icon, .flex .navbar-nav.small .nav-link:active .span {
  color: #FFFFFF;
}
.flex .navbar-nav.small .sidebar-heading {
  text-align: center;
}
.flex .navbar-nav.small .lateral {
  transform: rotate(180deg);
}

.sidebar-brand {
  display: flex;
  padding: 2.4rem 1.6rem;
  justify-content: center;
  align-items: center;
}
.sidebar-brand .sidebar-brand-icon {
  font-size: 3.5rem;
  color: #FFFFFF;
}
.sidebar-brand .sidebar-brand-text {
  display: none;
  transition: 0.3s;
  color: #FFFFFF;
  font-weight: 700;
}
@media (min-width: 768px) {
  .sidebar-brand .sidebar-brand-text {
    display: block;
  }
}
.sidebar-brand .sidebar-brand-text p {
  font-size: 1.6rem;
}

.siderbar-divider {
  margin: 0 1.6rem 1.6rem;
}
.siderbar-divider.my-0 {
  margin: 0 1.6rem 0;
}

hr {
  border-top: 1px solid rgba(255, 255, 255, 0.15);
  border-bottom: 1px rgba(255, 50, 50, 0);
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

.nav-item {
  position: relative;
  color: #FFFFFF;
  font-weight: 700;
}
.nav-item .nav-link {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: 0.3s;
  text-align: left;
  padding: 1.6rem;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .nav-item .nav-link {
    display: block;
    width: 22.5rem;
  }
}
.nav-item .nav-link:hover .bx-icon, .nav-item .nav-link:hover .span, .nav-item .nav-link:visited .bx-icon, .nav-item .nav-link:visited .span, .nav-item .nav-link:focus .bx-icon, .nav-item .nav-link:focus .span, .nav-item .nav-link:active .bx-icon, .nav-item .nav-link:active .span {
  color: #FFFFFF;
}
.nav-item .nav-link .bx-icon {
  font-size: 2rem;
  color: rgba(255, 255, 255, 0.5);
}
@media (min-width: 768px) {
  .nav-item .nav-link .bx-icon {
    font-size: 1.6rem;
  }
}
.nav-item .nav-link .bx-icon:hover {
  color: #FFFFFF;
}
.nav-item .nav-link .span {
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.8);
  font-weight: 600;
}
@media (min-width: 768px) {
  .nav-item .nav-link .span {
    font-size: 1.4rem;
    font-weight: 300;
  }
}
.nav-item .nav-link .bx-arrow {
  font-size: 1.5rem;
  display: none;
  transition: 0.3s;
  color: rgba(255, 255, 255, 0.5);
  margin: 0.8rem;
  float: right;
  vertical-align: 0;
}
@media (min-width: 768px) {
  .nav-item .nav-link .bx-arrow {
    display: block;
  }
}
.nav-item .nav-link .bx-arrow.girar {
  transform: rotate(90deg);
}

.lateral {
  visibility: hidden;
  text-align: center;
}
.lateral i {
  cursor: pointer;
  padding: 1rem;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.2);
  font-size: 3rem;
  color: rgba(255, 255, 255, 0.5);
}
.lateral i:hover {
  background-color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .lateral {
    visibility: visible;
  }
}

.sidebar-heading {
  color: rgba(255, 255, 255, 0.4);
  padding: 0 1.6rem;
  font-weight: 800;
  font-size: 1rem;
  text-transform: uppercase;
  text-align: center;
  transition: 0.3s;
}
@media (min-width: 768px) {
  .sidebar-heading {
    text-align: left;
  }
}

.nav {
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.contenedor {
  width: 100%;
  flex-direction: column;
  background-color: #F8F9FC;
}
.contenedor .header {
  margin: 0;
  background-color: #FFFFFF;
  box-shadow: 0 0.15rem 1.75rem 0 rgba(58, 59, 69, 0.15);
  height: 7rem;
}

.menu {
  visibility: visible;
  opacity: 1;
  height: 4rem;
  width: 4rem;
  cursor: pointer;
  color: #4e73df;
  text-align: center;
  padding: 0;
  border-radius: 50%;
  appearance: none;
  border: 0;
  background-color: #FFFFFF;
  transition: 0.3s;
}
.menu:hover {
  background-color: #EAECF4;
}
@media (min-width: 768px) {
  .menu {
    visibility: hidden;
    opacity: 0;
  }
}
.menu i {
  font-size: 2.5rem;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.8rem 1.6rem;
  height: 100%;
  transition: 0.3s;
}
.navbar .navbar-ul {
  display: flex;
  justify-content: center;
  align-items: center;
}
.navbar .navbar-ul .bloque-nav {
  border-right: 1px solid #e3e6f0;
  height: 3rem;
  margin: 0 2rem;
  width: 0;
}

.nav-caja {
  overflow: hidden;
  opacity: 0;
  transform: scale(0.7);
  visibility: hidden;
  border-radius: 0.5rem;
  background-color: #FFFFFF;
  position: absolute;
  top: 0;
  left: 10.75rem;
  z-index: 1;
  transition: 0.3s;
}
.nav-caja.mostrar {
  box-shadow: 0 0.15rem 1.75rem 0 rgba(58, 59, 69, 0.15);
  opacity: 1;
  transform: scale(1);
  visibility: visible;
}
@media (min-width: 768px) {
  .nav-caja {
    left: auto;
    height: 0;
    overflow: hidden;
    margin: 0 2rem 1rem;
    position: relative;
    z-index: 1;
    transform: scale(1);
  }
  .nav-caja.mostrar {
