.nav-item-li .mensajes {
  position: absolute;
  opacity: 0;
  visibility: hidden;
  transform: scale(0.7);
  transition: 0.3s;
  padding: 0;
  border: none;
  overflow: hidden;
  top: 6rem;
  right: -8rem;
  width: calc(100vw - 5rem);
